.ozzy-WindowComponent {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 800px;
    height: 400px;
    -webkit-box-shadow: #000000 0 0 50px;
            box-shadow: #000000 0 0 50px;
    border-radius: 5px;
    overflow: hidden;
}

.ozzy-WindowComponent .ozzy-WindowHeader {
    display: -ms-flexbox;
    display: flex;
    height: 23px;
    position: relative;

    background: #e7e6e7;
    background: -webkit-linear-gradient(top, #e7e6e7 0%, #d1d0d1 100%);
    background: -webkit-gradient(linear, left top, left bottom, from(#e7e6e7), to(#d1d0d1));
    background: -o-linear-gradient(top, #e7e6e7 0%, #d1d0d1 100%);
    background: linear-gradient(to bottom, #e7e6e7 0%, #d1d0d1 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#e7e6e7', endColorstr='#d1d0d1', GradientType=0);

    -webkit-box-sizing: border-box;

            box-sizing: border-box;
    border-top: #D5D5D5 1px groove;
    border-left: #D5D5D5 1px solid;
    border-right: #D5D5D5 1px solid;
    border-bottom: #B2B1B2 1px solid;
}

.ozzy-WindowComponent .ozzy-WindowHeader .ozzy-WindowTitle {
    height: 23px;
    line-height: 23px;
    -ms-flex: 1 1;
        flex: 1 1;
    text-align: center;
    font-size: 14px;
    color: #4D4D4D;
}

.ozzy-WindowComponent .ozzy-WindowHeader .ozzy-WindowButtons{
    display: -ms-flexbox;
    display: flex;
    position: relative;
    left: 0;
}

.ozzy-WindowComponent .ozzy-WindowHeader .ozzy-WindowButtons .redButton{
    margin-top: 5px;
    margin-left: 7px;
    width: 13px;
    height: 13px;
    background: #FF6059;
    border-radius: 50%;
}

.ozzy-WindowComponent .ozzy-WindowHeader .ozzy-WindowButtons .yellowButton{
    margin-top: 5px;
    margin-left: 7px;
    width: 13px;
    height: 13px;
    background: #FFBD2E;
    border-radius: 50%;
}

.ozzy-WindowComponent .ozzy-WindowHeader .ozzy-WindowButtons .greenButton{
    margin-top: 5px;
    margin-left: 7px;
    width: 13px;
    height: 13px;
    background: #28CA42;
    border-radius: 50%;
}

.ozzy-WindowComponent .ozzy-WindowContent {
    -ms-flex: 1 1;
        flex: 1 1;
    background: #171717;
    color: white;
    padding: 10px;
}

.ozzy-WindowComponent .ozzy-WindowContent .textLine {
    font-size: 14px;
    margin-bottom: 10px;
    height: 14px;
    line-height: 14px;
    vertical-align: text-top;
}

.ozzy-WindowComponent .ozzy-WindowContent a,
.ozzy-WindowComponent .ozzy-WindowContent a:link,
.ozzy-WindowComponent .ozzy-WindowContent a:active,
.ozzy-WindowComponent .ozzy-WindowContent a:visited {
    color: #005cbb;
}
.ozzy-WindowComponent .ozzy-WindowContent a:hover {
    color: white;
}
.ozzy-WindowComponent .ozzy-WindowContent .blink_me {
    display: inline-block;
    width: 8px;
    height: 14px;
    vertical-align: text-bottom;

    background: white;
    -webkit-animation-name: blinker;
    -webkit-animation-duration: 1s;
    -webkit-animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-name: blinker;
    -moz-animation-duration: 1s;
    -moz-animation-timing-function: linear;
    -moz-animation-iteration-count: infinite;
    animation-name: blinker;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}

.ozzy-WindowComponent .ozzy-WindowContent .tilda {
    margin-left: 5px;
    margin-right: 5px;
}
@-webkit-keyframes blinker {
    0% {
        opacity: 1.0;
    }
    50% {
        opacity: 0.0;
    }
    100% {
        opacity: 1.0;
    }
}
@keyframes blinker {
    0% {
        opacity: 1.0;
    }
    50% {
        opacity: 0.0;
    }
    100% {
        opacity: 1.0;
    }
}


